
:root {
  /*--primary: #555BFF;
  --secondary: #22D2A0;  */
  --main: #444cf7;
  --blue: #1343e6;
  --blue-light: #444cf7;
  --orange: #FFA500;
  --orange-light: #FF9C55;
  --purple: #7a4ef4;
  --purple-gray: #a799cc;
  --yellow: #fdee21;
  --yellow-light: #FFD913;
  --green: #00f4b7;
  --green-light: #1FC11B;
  --red: #F23838;
  --red-light: #FF5555;
  --pink: #eaafd0;
  --gray: #c0ccda;
  --gray-light: #e0e6ed;
  --gray-lightest: #f2f2f2;
  --gray-dark: #333;
  --background-color: #e5e5f7;
  --primary: var(--yellow);
  --secondary: var(--blue);
  --textColor: white;
  --play: running;
  --direction: normal;
  --duration: 60s;
  --delay: 0s;
  --iteration-count: infinite;
}

