/* static horizontal list open state */
* {
  //transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
}

html, body {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  --background-color: #eee;

}

body.credit-page {
  --background-color: var(--yellow);
}

.box-shadow {
  box-shadow: 10px -10px 0 -3px white, 10px -10px var(--green), 20px -20px 0 -3px white, 20px -20px var(--yellow), 30px -30px 0 -3px white, 30px -30px var(--orange), 40px -40px 0 -3px white, 40px -40px var(--red)
}
.shake {
  animation: swipe-x 2.25s ease-in-out backwards;
	animation-iteration-count:infinite;
  transform-origin: 25% 50%;
}

@keyframes swipe-x {
	0% {
		transform: translateX(0px);
	}
	25% {
		transform: translateX(50px) rotateZ(10deg);
	}
	50% {
		transform: translateX(0px);
	}
	75% {
		transform: translateX(-50px) translateY(10px) rotateZ(-10deg);
	}
}
.onboarding {
  background: rgba(0,0,0,.8);
}
div.static2 {
  width: 100vw!important;
  height: 100vh!important;
}
.h-gallery, .logo, .report {
  perspective: 350px;
  perspective-origin: 50% 50%;
  transition: height .5s ease;
}
div.static, div.static2 {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9;
    width: auto;
    height: 0;
    overflow:hidden;
    transition: all .01s ease;
    transition-delay: .01s;
    perspective: 350px;
    perspective-origin: 50% 50%;
    .question-window {
      > div {
        will-change: transform;
      }
    }
    .question-window > div {
      > div {
        background-size: auto 85%;
        background-repeat: no-repeat;
        background-position: center center;
        max-width: 500px;
        height: 85vh;
        max-height: 570px;
        will-change: transform;
        border: 7px solid var(--gray-dark);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        cursor: hand;

      }
    }

}
div.static span.close {
  display: inline-block;
  position: fixed;
  right: 10vw;
  top: 3vh;
  z-index: 9;
  color: white;
}
.list-open .static {
  height: 100vh;
  overflow: hidden;
}
div.static[open] {
    height: 100vh;
    width: 100vw;
}

div.static[open="0"] {
    height: 0;
}

