.large-rect {
  width: 350px;
  height: 450px;
  border: 1px solid var(--purple-gray);
  z-index: 0;
}

.large-rect > span {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid var(--blue);
  box-shadow: none;
}

.tall-rect {
  width: 200px;
  height: 400px;
  > span {
    width: 100px;height: 100px;
    border: 4px solid var(--blue);
  }
}

figcaption.hovered {
  transform: translateX(100px);
  transition: all .2s ease-out;
}

.large-rect, .tall-rect, .large-wide-rect {
  > span:hover {
    border-radius: 0;
    animation: scaleUp 0.3s ease-out .4s forwards;
    box-shadow: 10px -10px 0 -3px white, 10px -10px var(--green),
          20px -20px 0 -3px white, 20px -20px var(--yellow),
          30px -30px 0 -3px white, 30px -30px var(--orange),
          40px -40px 0 -3px white, 40px -40px var(--red);
  }
}

.large-wide-rect {
  > span:hover {
    animation: scaleUp2 0.3s ease-out .4s forwards;
  }
}

@keyframes scaleUp {
    0% {
        transform: scale(1.5) rotate(0);
        transform-origin: center;
    }
    100% {
        transform: scale(1.5) rotate(315deg);
        /*background-blend-mode:difference;*/
    }
}

@keyframes scaleUp2 {
    0% {
        transform: scale(1.5) rotate(0);
        transform-origin: center;
    }
    100% {
        transform: scale(1.5) rotate(360deg);
        /*background-blend-mode:difference;*/
    }
}

@keyframes square-animation {
    0% {
        transform: rotate(0);
        transform-origin: center;
    }
    100% {
        transform: rotate(315deg);
        //background-blend-mode:difference;
    }
}
.large-wide-rect {
  width: 450px;
  height: 300px;
  border: 1px solid var(--purple-gray);
  z-index: 0;
  > span {
    width: 150px;
    height: 100px;
    border: 4px solid var(--blue);
