}

.hexagon {
  position: relative;
  width: 300px;
  height: 173.21px;
  margin: 86.60px 0;
  border-left: solid 30px var(--blue);
  border-right: solid 30px var(--blue);
  box-shadow: 10px -10px 0 -3px white, 10px -10px var(--green),
        20px -20px 0 -3px white, 20px -20px var(--yellow),
        30px -30px 0 -3px white, 30px -30px var(--orange),
        40px -40px 0 -3px white, 40px -40px var(--red);
}

.hexagon:hover {
  box-shadow: 0 0 0 -10px white, 0 0 var(--green),
        0 0 0 -10px white, 0 0 var(--yellow),
        0 0 0 -10px white, 0 0 var(--orange),
        0 0 0 -10px white, 0 0 var(--red);
}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  z-index: 1;
  width: 212.13px;
  height: 212.13px;
  -webkit-transform: scaleY(0.5774) rotate(-45deg);
  -ms-transform: scaleY(0.5774) rotate(-45deg);
  transform: scaleY(0.5774) rotate(-45deg);
  left: 13.9340px;
  background-color: transparent;
}

.hexagon:before {
  top: -106.0660px;
  border-top: solid 42.4264px #333333;
  border-right: solid 42.4264px #333333;
  background-color: transparent;
}

.hexagon:after {
  bottom: -106.0660px;
  border-bottom: solid 42.4264px #333333;
  border-left: solid 42.4264px #333333;
}

