.stepper-dot-container {
  transition: all .8s easeOut;
  .stepper-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: var(--gray);
  }
  svg {
    display: none;
    fill: currentColor;
    width: 1em;
    height: 1em;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
    color: var(--green);
    z-index: 1;
    font-size: 24px;
  }
  &.active {
    .stepper-dot {
      background-color: var(--green);
      width: 12px;
      height: 12px;
    }
  }
  &.completed {
    .stepper-dot  {
      display: none;
    }
    svg {
      display: inline-block;
    }
  }
  &.completed.active {
    svg {
      border: 1px solid;
      border-radius: 50%;
    }
  }
}
.stepper-line-container {
  top: 10px;
  left: calc(-50% + 16px);
  right: calc(50% + 16px);
}
.stepper-line {
  border-color: var(--gray-light);
  border-radius: 1px;
  border-top-width: 3px;
  border-top-style: solid;
  &.completed {
    border-color: var(--green)
  }
}

