.shake{
  -webkit-animation:swipe-x 2.25s ease-in-out backwards;
  animation:swipe-x 2.25s ease-in-out backwards;
  -webkit-animation-iteration-count:infinite;
  animation-iteration-count:infinite;
  -webkit-transform-origin:25% 50%;
  transform-origin:25% 50%
}

@-webkit-keyframes swipe-x{
  0%{
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }

  25%{
    -webkit-transform:translateX(50px) rotateZ(10deg);
    transform:translateX(50px) rotateZ(10deg)
  }

  50%{
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }

  75%{
    -webkit-transform:translateX(-50px) translateY(10px) rotateZ(-10deg);
    transform:translateX(-50px) translateY(10px) rotateZ(-10deg)
  }
}

@keyframes swipe-x{
  0%{
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }

  25%{
    -webkit-transform:translateX(50px) rotateZ(10deg);
    transform:translateX(50px) rotateZ(10deg)
  }

  50%{
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }

  75%{
    -webkit-transform:translateX(-50px) translateY(10px) rotateZ(-10deg);
    transform:translateX(-50px) translateY(10px) rotateZ(-10deg)
  }
}

