.scroll-container {
  overflow: hidden;
  white-space: nowrap;
  li {
    width: 200px;
    height: 300px;
    margin-right: 2rem;
    display: inline-block;
    &:first-of-type {
      margin-left: 3rem;
    }
    border: 5px solid black;
  }
}
.more-answer {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.more-answer-arrow {
  width: .8rem;
  height: .8rem;
  border-top: .2rem solid;
  border-right: .2rem solid;
  transform: rotate(45deg);
}
